body {
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

* {
  box-sizing: border-box;
}

ul, li {
  list-style: none;
  padding: 0;
}

nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  align-items: center;
  border-bottom: 1px solid #ddd;
}

footer {
  position: fixed;
  display: block;
  width: 100%;
  height: 50px;
  bottom: 0;
  left: 0;
  padding: 0 20px;
  text-align: center;
  border-top: 1px solid #ddd;
  background: #fff;
  z-index: 3;
}

main {
  max-width: 1200px;
  margin: 0 auto;
  padding-bottom: 50px;
}


button.disable {
  opacity: .3;
  background-color: rgb(228, 228, 228)!important;
  color: rgb(87, 87, 87)!important;
  user-select: none;
  cursor: default !important;
}

#basket-list-container {
  max-width: 400px;
  max-height: 400px;
  position: absolute;
  top: 75px;
  right: 20px;
  border-radius: 4px;
  box-shadow: 0 22px 48px -12px rgba(0,0,0,0.12);
  overflow-y: auto;
  background: #fff;
  z-index: 2;
}

.brand {
  font-size: 22px;
  font-weight: bold;
  font-family: sans-serif;
  color: #333;
}

.hidden {
  display: none;
}

.basket-link {
  border-radius: 5px;
  width: 136px;
  line-height: 48px;
  text-align: center;
  display: block;
  background-color:#919191;
  font-size: 14px;
  color: #fff;
  position: relative;
  text-decoration: none;
  cursor: pointer;
}

.flex-wrap {
  display: flex;
  flex-wrap: wrap;
}

.card {
  display: flex;
  flex-direction: column;
  min-height: 300px;
  width: 31%;
  margin: 1%;
  text-align: left;
  color: #333;
  display: block;
  box-shadow: 0 22px 48px -12px rgba(0,0,0,0.12);
  border-radius: 5px;
  overflow: hidden;
  transition: all .3s ease;

  @media (max-width: 768px) {
    width: 48%;
  }

  @media (max-width: 481px) {
    width: 100%;
  }
}

.imgWrapper {
  position: relative;
  display: block;
  width: auto;
  height: 250px;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain
  }
}

.product-desc {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px 10px 0;
}


.img {
  max-width: 100%;
  max-height: 300px;
  object-fit: contain;
}

.title {
  margin-top: 10px;
  margin-bottom: 10px;
}


.button {
  border-radius: 10px;
  font-size: 14px;
  line-height: 30px;
  background-color: #66ABFF;
  width: 50%;
  margin: 10px 0 10px 10px;
  text-align: center;
  color: #fff;
  font-weight: 600;
  border: none;
  outline: none !important;
  transition: all .3s ease-in-out 0s;
  cursor: pointer;

  &:hover {
    background-color: #4495d4;
  }
}


.basket-card {
  display: flex;
  width: 350px;
  padding: 10px 20px;

  .imgWrapper {
    width: 100px;
    height: 100px;
    margin-right: 10px;
    flex-shrink: 0;

    img {
      border-radius: 4px;
    }
  }

  .basket-desc {
    flex: 1;
  }

  p {
    font-size: 12px;
  }

  button {
    margin: 0;
    font-size: 12px;
    background-color: brown;
  }
}